/** @format */

import { useState, useEffect } from 'react';
import './tailwind.css';
import questions from './questions.js';
import AnimatedProgressBar from './AnimatedBar';
import purityProdigyImage from './imgs/PurityProdigy.png'; 
import daringDeviantImage from './imgs/DaringDeviant.png';
import wildWandererImage from './imgs/WildWanderer.png';
import rebelRogueImage from './imgs/RebelRogue.png';
import adventurousAngelImage from './imgs/AdventurousAngel.png';

function App() {
	const [checkedBoxes, setCheckedBoxes] = useState({});
	const [score, setScore] = useState(100);
	const [showScore, setShowScore] = useState(false); // State to track if score has been calculated
	const [title, setTitle] = useState('');
	const [description, setDescription] = useState('');
	const [image, setImage] = useState('');

	const handleCheckboxChange = (e) => {
		const { name, checked } = e.target;
		setCheckedBoxes({ ...checkedBoxes, [name]: checked });
	};

	const calculateScore = () => {
		try {
			const newScore = Object.values(checkedBoxes).reduce(
				(acc, cur) => (cur ? acc + 1 : acc),
				0,
			);
			setScore(100 - newScore);
			setShowScore(true); // Set showScore to true after calculating score
		} catch (error) {
			console.error('Error calculating score:', error);
		}
	};

	const getTitle = (score) => {
		if (score >= 80) return 'Purity Prodigy';
		else if (score >= 60) return 'Adventurous Angel';
		else if (score >= 40) return 'Daring Deviant';
		else if (score >= 20) return 'Wild Wanderer';
		else return 'Rebel Rogue';
	};

  const getImage = (score) => {
		if (score >= 80) return purityProdigyImage;
		else if (score >= 60) return adventurousAngelImage;
		else if (score >= 40) return daringDeviantImage;
		else if (score >= 20) return wildWandererImage;
		else return rebelRogueImage;
	};

	const getDescription = (score) => {
		if (score >= 80)
			return "You're so pure, you probably think a 'PG-13' movie is too risqu. You're like a unicorn in a sea of wild horses, and the world could use more of your innocence (and maybe a sprinkle of curiosity).";
		else if (score >= 60)
			return "You're the kind of person who's up for trying new things but still blushes at the mention of a naughty joke. You walk the line between curiosity and caution, always ready for a wholesome adventure.";
		else if (score >= 40)
			return "You've tasted the forbidden fruit but haven't strayed too far from the garden. You're like a mischievous puppy, always curious and playful, but with a hint of mischief in your eyes.";
		else if (score >= 20)
			return "You've explored some uncharted territories but haven't lost your sense of wonder. You're like a fearless explorer, venturing into the unknown while keeping a map of your moral compass close at hand.";
		else
			return "You've danced on the edge of convention, pushing boundaries while still keeping your rebellious spirit intact. You're like a renegade in a world of rules, adding a dash of spice to the recipe of life.";
	};

	useEffect(() => {
		setTitle(getTitle(score));
		setDescription(getDescription(score));
    setImage(getImage(score));
	}, [score]);

	const showHome = () => {
		setShowScore(false); // Set showScore to false to show checkboxes again
		setCheckedBoxes({}); // Reset the checked checkboxes state
		setScore(100); // Reset the score to 100

		// Uncheck all checkboxes on the page
		const checkboxes = document.querySelectorAll('input[type="checkbox"]');
		checkboxes.forEach((checkbox) => {
			checkbox.checked = false;
		});
	};

	const shareScore = async () => {
		if (navigator.share) {
			try {
				await navigator.share({
					title: 'Share Your Score',
					text: `My score on the purity test is ${score}!`,
					url: window.location.href,
				});
			} catch (error) {
				console.error('Error sharing score:', error);
			}
		} else {
			// Fallback behavior for browsers that don't support the navigator.share API
			const shareUrl = `https://neelriteshyadav.github.io/zoomasspuritytest/`;
			window.open(shareUrl, '_blank');
		}
	};

	return (
    <div className='bg-gray-100 min-h-screen flex flex-col justify-center items-center font-poppins w-full'>
      <h1 className='text-3xl font-bold mb-8 pt-10'>ZooMass Purity Test</h1>
      <div className='w-4/5 sm:w-4/5 lg:w-3/5 flex justify-center items-center'>
        Ready to uncover your 'ZooMass' purity? Take the ultimate college
        challenge  from hand-holding to streaking, we've got checkboxes for
        every questionable choice you've made. Find out if you're a saint or a
        party legend in just a click!
      </div>
      {showScore ? ( // Conditional rendering based on showScore state
        <div className='w-4/5 sm:w-4/5 lg:w-3/5 flex items-start justify-center flex-col pt-10 pb-10'>
          <AnimatedProgressBar score={score} />
  
          <div className='mt-8 text-center flex items-center justify-center flex-col'>
          <img src={image} alt='Score Cartoon' className='w-40 h-40 mb-4' />
            <h2 className='text-2xl font-bold'>{title}</h2>
            <p className='mt-2'>{description}</p>
          </div>
          <div className='mt-10 w-full sm:w-4/5 lg:w-3/5 flex flex-row space-x-3'>
            <button
              onClick={shareScore}
              className='bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded transition duration-300 ease-in-out transform hover:scale-110'>
              Share Score
            </button>
            <button
              onClick={showHome}
              className='bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded transition duration-300 ease-in-out transform hover:scale-110'>
              Give The Test Again
            </button>
          </div>
        </div>
      ) : (
        <div className='w-4/5 sm:w-4/5 lg:w-3/5'>
          <div className='font-semibold flex justify-center pt-5'>
            Caution: This is not a bucket list. Completion of all items on this
            test will likely result in death.
          </div>
          <div className='flex justify-center items-center pt-5 pb-5'>
            Click on every item you have done. MPS stands for Member of the Preferred Sex.
          </div>
          <div className='flex items-start justify-center flex-col'>
            {questions.map((question, index) => (
              <label
                key={index}
                className='flex items-center'>
                <input
                  type='checkbox'
                  name={`checkbox${index + 1}`}
                  onChange={handleCheckboxChange}
                  className='mr-2'
                />
                <span>{question}</span>
              </label>
            ))}
            <div className='flex space-x-5 pb-10'>
              <button
                className='mt-8 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded transition duration-300 ease-in-out transform hover:scale-110'
                onClick={calculateScore}>
                Calculate My Score
              </button>
              <button
                className='mt-8 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded transition duration-300 ease-in-out transform hover:scale-110'
                onClick={showHome}>
                Clear all checkboxes
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
  
}

export default App;
